body .ui-dialog {
    position: fixed;
    padding: 0;
}

body .ui-widget-header {
    height: 100%;
    background: #fcfcfc;
    border: 0;
    border-bottom: 1px solid #eaeaea;
    border-radius: 0 !important;
}

body .ui-state-default {
    height: 100%;
}

body .ui-dialog-titlebar {
    font-size: 16px;
}

body .ui-dialog .ui-dialog-titlebar-close {
    width: 30px;
    height: 30px;
    top: 16%;
}

body .ui-dialog-titlebar-close:before {
    line-height: 28px;
}

body .ui-widget-overlay {
    position: fixed;
    background: #000;
}

body .ui-widget-content {
    border: 0;
    border-top: 1px solid #eaeaea;
}

body .ui-dialog .ui-dialog-buttonpane {
    padding: 10px;
    text-align: center;
}

body .ui-dialog-buttonpane .ui-dialog-buttonset {
    float: none;
}

body .ui-dialog .ui-dialog-buttonpane button {
    float: none;
    border-radius: 25px;
}

.reign-ld-linked-group-list span {
    color: green;
    font-style: italic;
    display: none;
}

.reign-ld-spinner {
    margin: 5px 5px 0;
}

/* BP Group Member Sync */
#ld_bp_group_sync .submit {
    display: none;
}

/* reign-ld-group-pagination */
#reign-ld-pagination-bar ul {
    text-align: right;
}

#reign-ld-pagination-bar ul li {
    display: inline-block;
    margin-right: 4px;
}

#reign-ld-pagination-bar ul li a {
    display: block;
    text-decoration: none;
    font-size: 14px;
    cursor: pointer;
    width: 32px;
    height: 32px;
    line-height: 32px;
    background: #f4f4f4;
    color: #444;
    margin: 4px;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    transition: all .5s;
}

#reign-ld-pagination-bar ul li.active a,
#reign-ld-pagination-bar ul li a:hover {
    -webkit-box-shadow: 0 1.5px 8px 0 rgba(0, 0, 0, 0.3);
    -moz-box-shadow: 0 1.5px 8px 0 rgba(0, 0, 0, 0.3);
    -o-box-shadow: 0 1.5px 8px 0 rgba(0, 0, 0, 0.3);
    box-shadow: 0 1.5px 8px 0 rgba(0, 0, 0, 0.3);
}

/* Tabs icons set */
button.reign-tablinks:before {
    font-size: 20px;
}

button.reign-tablinks.ld_general:before {
    content: "\f108";
}

button.reign-tablinks.ld_related_course:before {
    content: "\f313";
}

button.reign-tablinks.ld_buddypress:before {
    content: "\f240";
}

button.reign-tablinks.ld_bp_group_sync:before {
    content: "\f463";
}